<template>
  <div class="cash-register">
    <van-nav-bar
      title="收银"
      left-text="返回"
      :fixed="true"
      left-arrow
      @click-left="onClickLeft"
    ></van-nav-bar>

    <div class="zhan"></div>
    <van-tabs v-model="active" color="#2a8ae8">
      <van-tab title="会员">
        <van-cell-group>
          <van-field
            v-model="memberNo"
            @input="numberInput"
            center
            clearable
            label="会员卡号"
            placeholder="请输入手机号"
            type="number"
          >
            <template #button>
              <van-button size="small" @click="autMemHandler" type="info"
                >验证</van-button
              >
            </template>
          </van-field>
        </van-cell-group>

        <van-cell-group>
          <van-cell title="姓名" :value="name" />
          <van-cell title="剩余金额" :value="money + ' 元'" />
        </van-cell-group>

        <van-cell-group>
          <van-field
            v-model="form.consumeMoney"
            type="number"
            placeholder="请输入"
            label="消费金额"
          />

          <van-field name="checkboxGroup" label="项目">
            <template #input>
              <van-checkbox-group v-model="projectArr" direction="horizontal">
                <van-checkbox
                  v-for="item in project"
                  :key="item.projectId"
                  :name="item.projectId"
                  shape="square"
                  >{{ item.projectName }}</van-checkbox
                >
              </van-checkbox-group>
            </template>
          </van-field>

          <!-- <van-field name="radio" label="项目">
            <template #input>
              <van-radio-group v-model="form.project" direction="horizontal">
                <van-radio
                  v-for="item in project"
                  :key="item.projectId"
                  :name="item.projectId"
                  >{{ item.projectName }}</van-radio
                >
              </van-radio-group>
            </template>
          </van-field> -->
        </van-cell-group>

        <van-cell-group>
          <van-field
            v-model="rechargeMoney"
            type="number"
            placeholder="请输入"
            label="充值金额"
          />
          <van-field
            v-model="extraMoney"
            type="number"
            placeholder="请输入"
            label="赠送金额"
          />
          <van-field name="radio" label="付款方式">
            <template #input>
              <van-radio-group v-model="payType1" direction="horizontal">
                <!-- <van-radio name="余额">余额</van-radio> -->
                <van-radio
                  v-for="item in paytypes"
                  :key="item.payType"
                  :name="item.payType"
                  >{{ item.payTypeName }}</van-radio
                >
              </van-radio-group>
            </template>
          </van-field>
        </van-cell-group>

        <van-cell-group>
          <van-field
            v-model="stylistName"
            is-link
            readonly
            label="大工"
            placeholder="请选择"
            @click="showPickerStylist = true"
          />
          <van-field
            v-model="colorerName"
            is-link
            readonly
            label="中工"
            placeholder="请选择"
            @click="showPickerColorer = true"
          />
          <van-field
            v-model="helperName"
            is-link
            readonly
            label="小工/助理"
            placeholder="请选择"
            @click="showPickerHelper = true"
          />
        </van-cell-group>

        <van-cell-group>
          <van-field
            v-model="form.remark"
            rows="2"
            autosize
            label="备注"
            type="textarea"
            maxlength="200"
            placeholder="请输入"
            show-word-limit
          />
        </van-cell-group>

        <div class="btn-wrap">
          <van-button type="info" block @click="recordHandler1"
            >提交</van-button
          >
        </div>
      </van-tab>

      <van-tab title="散客">
        <van-cell-group>
          <van-field
            v-model="form.consumeMoney"
            type="number"
            placeholder="请输入"
            label="金额"
          />

          <van-field name="checkboxGroup" label="项目">
            <template #input>
              <van-checkbox-group v-model="projectArr" direction="horizontal">
                <van-checkbox
                  v-for="item in project"
                  :key="item.projectId"
                  :name="item.projectId"
                  shape="square"
                  >{{ item.projectName }}</van-checkbox
                >
              </van-checkbox-group>
            </template>
          </van-field>

          <!-- <van-field name="radio" label="项目">
            <template #input>
              <van-radio-group v-model="form.project" direction="horizontal">
                <van-radio
                  v-for="item in project"
                  :key="item.projectId"
                  :name="item.projectId"
                  >{{ item.projectName }}</van-radio
                >
              </van-radio-group>
            </template>
          </van-field> -->

          <van-field name="radio" label="付款方式">
            <template #input>
              <van-radio-group v-model="payType2" direction="horizontal">
                <van-radio
                  v-for="item in paytypes"
                  :key="item.payType"
                  :name="item.payType"
                  >{{ item.payTypeName }}</van-radio
                >
              </van-radio-group>
            </template>
          </van-field>
        </van-cell-group>

        <van-cell-group>
          <van-field
            v-model="stylistName"
            is-link
            readonly
            label="大工"
            placeholder="请选择"
            @click="showPickerStylist = true"
          />
          <van-field
            v-model="colorerName"
            is-link
            readonly
            label="中工"
            placeholder="请选择"
            @click="showPickerColorer = true"
          />
          <van-field
            v-model="helperName"
            is-link
            readonly
            label="小工/助理"
            placeholder="请选择"
            @click="showPickerHelper = true"
          />
        </van-cell-group>

        <van-cell-group>
          <van-field
            v-model="form.remark"
            rows="2"
            autosize
            label="备注"
            type="textarea"
            maxlength="200"
            placeholder="请输入"
            show-word-limit
          />
        </van-cell-group>

        <div class="btn-wrap">
          <van-button type="info" block @click="recordHandler2"
            >提交</van-button
          >
        </div>
      </van-tab>
    </van-tabs>

    <van-popup v-model="showPickerStylist" round position="bottom">
      <van-picker
        show-toolbar
        :columns="staffSimple"
        @cancel="showPickerStylist = false"
        @confirm="onConfirmStylist"
      />
    </van-popup>

    <van-popup v-model="showPickerColorer" round position="bottom">
      <van-picker
        show-toolbar
        :columns="staffSimple"
        @cancel="showPickerColorer = false"
        @confirm="onConfirmColorer"
      />
    </van-popup>

    <van-popup v-model="showPickerHelper" round position="bottom">
      <van-picker
        show-toolbar
        :columns="staffSimple"
        @cancel="showPickerHelper = false"
        @confirm="onConfirmHelper"
      />
    </van-popup>
  </div>
</template>

<script>
import { queryMemberByNo, recordAdd } from '@/api/index';
import { checkMobile } from '@/utils/index';

import { mapState, mapGetters } from 'vuex';

export default {
  name: 'CashRegister',
  components: {},
  data() {
    return {
      active: 2,

      showPickerStylist: false, // 造型师下拉框
      showPickerColorer: false, // 烫染师下拉框
      showPickerHelper: false, // 助理下拉框

      memberNo: '',
      name: '--',
      money: '--',

      payType1: '',
      payType2: '',
      rechargeMoney: '', // 充值金额
      extraMoney: '', // 赠送金额
      stylistName: '', // 造型师
      colorerName: '', // 烫染师
      helperName: '', // 助理
      projectArr: [],
      form: {
        consumeMoney: '',
        project: '',
        remark: ''
      }
    };
  },
  watch: {
    projectArr(val) {
      this.form.project = val.join();
    }
  },
  computed: {
    ...mapState(['project', 'paytypes', 'userInfo']),

    ...mapGetters(['staffSimple', 'staffSimpleMap'])
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    // 会员验证
    autMemHandler() {
      if (checkMobile(this.memberNo).code) {
        this.$Toast('请输入会员电话号码');
        return;
      }
      queryMemberByNo(this.memberNo).then(res => {
        if (res) {
          if (res.data.code === 0) {
            const { memberName, money } = res.data.data;
            this.name = memberName;
            this.money = money;
          } else {
            this.$Toast(res.data.msg);
          }
        } else {
          this.$Toast('验证失败');
        }
      });
    },
    recordHandler(param = {}) {
      recordAdd({
        ...this.form,
        ...param,
        stylistId: this.staffSimpleMap[this.stylistName],
        colorerId: this.staffSimpleMap[this.colorerName],
        helperId: this.staffSimpleMap[this.helperName],
        createBy: this.userInfo.loginName,
        deptId: this.userInfo.deptId
      }).then(res => {
        if (res) {
          if (res.data.code === 0) {
            this.$Toast('提交成功');
            this.$router.go(-1);
          } else {
            this.$Toast(res.data.msg);
          }
        }
      });
    },
    // 会员收银提交
    recordHandler1() {
      if (this.money === '--') {
        this.$Toast('请先验证会员');
        return;
      }

      if (!this.form.consumeMoney) {
        this.$Toast('请输入消费金额');
        return;
      }

      if (!this.form.project) {
        this.$Toast('请选择项目');
        return;
      }

      if (this.rechargeMoney) {
        if (!this.payType1) {
          this.$Toast('请选择付款方式');
          return;
        }
      }

      if (!this.stylistName) {
        this.$Toast('请选择大工');
        return;
      }
      this.recordHandler({
        memberNo: this.memberNo,
        payType: this.payType1,
        rechargeMoney: this.rechargeMoney,
        extraMoney: this.extraMoney,
        consumeType: 1
      });
    },
    // 非会员收银提交
    recordHandler2() {
      if (!this.form.consumeMoney) {
        this.$Toast('请输入消费金额');
        return;
      }

      if (!this.form.project) {
        this.$Toast('请选择项目');
        return;
      }

      if (!this.payType2) {
        this.$Toast('请选择付款方式');
        return;
      }

      if (!this.stylistName) {
        this.$Toast('请选择大工');
        return;
      }
      this.recordHandler({ payType: this.payType2, consumeType: 2 });
    },
    // 大工选定
    onConfirmStylist(value) {
      this.stylistName = value;
      this.showPickerStylist = false;
    },
    // 中工选定
    onConfirmColorer(value) {
      this.colorerName = value;
      this.showPickerColorer = false;
    },
    // 小工/助理选定
    onConfirmHelper(value) {
      this.helperName = value;
      this.showPickerHelper = false;
    },
    // 电话号码输入
    numberInput() {
      this.name = '--';
      this.money = '--';
    }
  }
};
</script>


